.Login-Container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed; 
  left: 0; 
  top: 0;
  width: 100%;
  height: 100%; 
}

.Login {
  padding: 30px;
  background-color: #e0dfd6;
  /* margin: 0 auto 10px; */
  border-radius: 8px;
  box-shadow: 0px 0px 1px #181615;
  overflow: hidden;
}

.Inputs input {
  height: 44px;
  font-size: 16px;
  width: 100%;
  margin-bottom: 10px;
  -webkit-appearance: none;
  background: #fff;
  border: 1px solid #d9d9d9;
  /* border-top: 1px solid #c0c0c0; */
  /* border-radius: 2px; */
  padding: 0 8px;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
    border-radius: 25px;
}

